import { bindings, hook } from '@vl/redata';
import _ from 'lodash';
import useRoute from '@vl/hooks/useGbRoute';

const bindData = bindings({
  FilterForm: {
    rules: [
      [
        'data',
        {
          data: {
            priceRange: hook((ctx) => {
              return ctx.get('priceRangeOptions');
            }),
            form: hook((ctx) => {
              return ctx.get('advisorFilterForm');
            }),
            canSubmit: hook((ctx) => {
              return ctx.get('form.isValid') && !ctx.get('form.isSubmitting');
            }),
            categoryOptions: hook((ctx) => {
              const categories = ctx.get('categories', []);
              return categories;
            }),
            nameCategory: hook((ctx) => {
              const route = useRoute();
              const routeParams = _.get(route, 'params', {});
              const categories = _.compact(_.castArray(_.get(routeParams, 'category', '')));
              const categoryOptionsById = _.keyBy(ctx.get('categoryOptions', []), 'id');
              return categories.map((item) => _.get(categoryOptionsById, `${item}.display_name`)).join(', ');
            })
          }
        }
      ]
    ]
  }
});

export default bindData;
