import React from 'react';

import { ctx } from '@vl/redata';
import DIV from '@vl/redata/DIV.macro';
import HeroFilterSection from '@uz/unitz-components-web/HeroFilterSection';
import FilterForm from '@uz/unitz-components-web/FilterForm';
import AdvisorListY from '@uz/unitz-components-web/AdvisorListY';
import CategoryListSuggestion from '@uz/unitz-components-web/CategoryListSuggestion';
import { resable } from '@vl/redata/RESABLE';
import RESABLE from '@vl/redata/RESABLE.macro';

const Index = () => {
  return (
    <DIV className="pageContainer">
      <div className="relative none-container">
        <HeroFilterSection />
      </div>
      <div className="pb-0 wrapper app-row">
        <div className="py-0">
          <RESABLE>
            <DIV>
              {ctx.debug(() => {
                const categories = resable.for('categories')(ctx.get('categories'), []);
                ctx.set('categories', categories);
              })}
              <FilterForm />
            </DIV>
          </RESABLE>
        </div>

        <div className="my-2">
          <p className="font-normal text-base sm:font-semibold text-main">
            {ctx.get('advisorFilterPagingModel.total')} {ctx.apply('i18n.t', 'Selection.result')}
          </p>
          <div className="block sm:hidden border-t border-divider" />
        </div>
      </div>
      <div className="sm:wrapper app-row">
        <AdvisorListY />
      </div>
      <div className="none-container">
        <RESABLE>
          <DIV>
            {ctx.debug(() => {
              const categories = resable.for('categories')(ctx.get('categories'), []);
              ctx.set('categories', categories);
            })}
            <CategoryListSuggestion />
          </DIV>
        </RESABLE>
      </div>
    </DIV>
  );
};

export default Index;
